import { selectCartItems, selectCartItemsCount } from '../../../../redux/cart/cart.selector';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { forwardRef } from 'react';
import CartMenuItem from '.././CartMenuItem';
import React from 'react';
import './styles.css';

const CartMenu = forwardRef(({ cartItems, cartCount, openItemDetails, list }, ref) => {
  return (
    cartCount !== 0 && cartItems.find((cartItem) => String(cartItem.id)[0] == 8) 
    ?
    <main className='menu-main' ref={ref}>
      {list.map((item) => (
        <CartMenuItem item={item} key={item.id} />
      ))}
    </main>
    :
    null
  )
});

const mapStateToProps = createStructuredSelector({
  cartItems: selectCartItems,
  cartCount: selectCartItemsCount,
});

export default connect(mapStateToProps)(CartMenu);
